import { useCallback, useEffect, useState } from "react";
import { Box, Container, Flex } from '@chakra-ui/react';
import ChessSquare from "./ChessSquare";

function Board() {

  const [squares, setSquares] = useState([]);
  
  const setInitialPositions = useCallback((squaresList) => {
    const whitePawns = ['A2', 'B2', 'C2', 'D2', 'E2', 'F2', 'G2', 'H2'];
    const blackPawns = ['A7', 'B7', 'C7', 'D7', 'E7', 'F7', 'G7', 'H7'];
    const whiteRooks = ['A1', 'H1'];
    const blackRooks = ['A8', 'H8'];
    const whiteKing = ['E1'];
    const blackKing = ['E8'];
    const whiteQueen = ['D1'];
    const blackQueen = ['D8'];
    const whiteBishops = ['C1', 'F1'];
    const blackBishops = ['C8', 'F8'];
    const whiteKnights = ['B1', 'G1'];
    const blackKnights = ['B8', 'G8'];

    for (let i = 0; i < squaresList.length; i++) { 
      for(let j = 0; j < whitePawns.length; j++) {
        if(whitePawns[j] === squaresList[i].coordinates) {
          squaresList[i].piece = 'whitePawn';
        }
      }
      for(let j = 0; j < blackPawns.length; j++) {
        if(blackPawns[j] === squaresList[i].coordinates) {
          squaresList[i].piece = 'blackPawn';
        }
      }
      for(let j = 0; j < whiteRooks.length; j++) {
        if(whiteRooks[j] === squaresList[i].coordinates) {
          squaresList[i].piece = 'whiteRook';
        }
      }
      for(let j = 0; j < blackRooks.length; j++) {
        if(blackRooks[j] === squaresList[i].coordinates) {
          squaresList[i].piece = 'blackRook';
        }
      }
      for(let j = 0; j < whiteKing.length; j++) {
        if(whiteKing[j] === squaresList[i].coordinates) {
          squaresList[i].piece = 'whiteKing';
        }
      }
      for(let j = 0; j < blackKing.length; j++) {
        if(blackKing[j] === squaresList[i].coordinates) {
          squaresList[i].piece = 'blackKing';
        }
      }
      for(let j = 0; j < whiteQueen.length; j++) {
        if(whiteQueen[j] === squaresList[i].coordinates) {
          squaresList[i].piece = 'whiteQueen';
        }
      }
      for(let j = 0; j < blackQueen.length; j++) {
        if(blackQueen[j] === squaresList[i].coordinates) {
          squaresList[i].piece = 'blackQueen';
        }
      }
      for(let j = 0; j < whiteBishops.length; j++) {
        if(whiteBishops[j] === squaresList[i].coordinates) {
          squaresList[i].piece = 'whiteBishop';
        }
      }
      for(let j = 0; j < blackBishops.length; j++) {
        if(blackBishops[j] === squaresList[i].coordinates) {
          squaresList[i].piece = 'blackBishop';
        }
      }
      for(let j = 0; j < whiteKnights.length; j++) {
        if(whiteKnights[j] === squaresList[i].coordinates) {
          squaresList[i].piece = 'whiteKnight';
        }
      }
      for(let j = 0; j < blackKnights.length; j++) {
        if(blackKnights[j] === squaresList[i].coordinates) {
          squaresList[i].piece = 'blackKnight';
        }
      }
    }
    // console.log('output set ', squaresList)
    setSquares(squaresList)
  }, [])

  useEffect(() => {
    const squaresList = [];
    const alphabet = 'ABCDEFGH';
    let color = '#e8dab5';
    let colCount = 0;
    let rowCount = 8;
    for(let square = 1; square < 65; square++) {
      if(colCount > alphabet.length-1) {
        colCount = 0;
        rowCount--;
      }
      squaresList.push({coordinates: `${alphabet[colCount]}${rowCount}`, color: color, piece: null})
      colCount++
      if(colCount <= alphabet.length-1) {
        color = color === '#91670a' ? '#e8dab5': '#91670a';
      }
    }
    // setSquares(squaresList)
    setInitialPositions(squaresList)
    // console.log(squaresList)
  }, [setInitialPositions])

  const dragStart = (e) => {
    this.dragged = e.currentTarget;
    e.dataTransfer.effectAllowed = 'move';
    e.dataTransfer.setData('text/html', this.dragged);
  }

  const dragEnd = (e) => {
  //   this.dragged.style.display = 'block';
  //   this.dragged.parentNode.removeChild(placeholder);
    
  //   // update state
  //   var data = this.state.colors;
  //   var from = Number(this.dragged.dataset.id);
  //   var to = Number(this.over.dataset.id);
  //   if(from < to) to--;
  //   data.splice(to, 0, data.splice(from, 1)[0]);
  //   this.setState({colors: data});
  }

  // const dragOver = (e) => {
  //   e.preventDefault();
  //   this.dragged.style.display = "none";
  //   if(e.target.className === 'placeholder') return;
  //   this.over = e.target;
  //   e.target.parentNode.insertBefore(placeholder, e.target);
  // }

  return (
    <Container
      display='flex'
      flexDirection='row'
    >
      <Box
        width='24px'
        height='320px'
        margin='10px 0px 10px 0px'
        alignItems='center'
        justifyContent='center'
      >
        {[8,7,6,5,4,3,2,1].map((num, i) => (
           <Box
            key={i}
            bg="white"
            height="36px"
            display="flex"
            alignItems="center"
            justifyContent="center"
           >
             {num}
           </Box>
        ))}
      </Box>
      <Flex
        flexDirection='column'
      >
        <Box
          bg='grey'
          width='288px'
          height='288px'
          display='flex'
          flexWrap='wrap'
          margin='10px'
          flexDirection='col'
        >
          {squares.map((sq, i) => (
            <ChessSquare 
              key={i}
              color={sq.color}
              piece={sq.piece}  
              coordinates={sq.coordinates}
              dragStart={dragStart} 
              dragEnd={dragEnd}
            />
          ))}
        </Box>
        <Box
          display="flex"
          width='288px'
          height='36px'
          margin='0px 10px 0px 10px'
          alignItems='center'
          justifyContent='center'
        >
          {['A', 'B', 'C', 'D', 'E', 'F', 'G', 'H'].map((col, i) => (
            <Box
              key={i}
              bg="white"
              height="36px"
              width="36px"
              display="flex"
              alignItems="center"
              justifyContent="center"
            >
              {col}
            </Box>
          ))}
        </Box>
      </Flex>
    </Container>
  )
}

export default Board;