import React from 'react';
import {
    Box,
    Text,
    Slider,
    SliderTrack,
    SliderFilledTrack,
    SliderThumb,
} from '@chakra-ui/react';
import { SunIcon } from '@chakra-ui/icons'


function SkillRating(props) {

    return (
      <React.Fragment>
          <Text
            fontSize='md'>
            {props.skillName}
          </Text>
          <Slider aria-label='slider-ex-4' isReadOnly value={props.skillRating}>
            <SliderTrack bg='red.100'>
              <SliderFilledTrack bg='tomato' />
            </SliderTrack>
            <SliderThumb boxSize={6}>
              <Box color='tomato' as={SunIcon} />
            </SliderThumb>
          </Slider>
      </React.Fragment>
    )
} 

export default SkillRating;