import {Flex, Box} from '@chakra-ui/react';
import { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom'

function ProjectItems(props) {
  const [items, setItems] = useState([])
  const navigate = useNavigate();
  useEffect(() => (
        setItems(props.items)
  ), [props])
 
  const handleClick = (event) => {
    if(event.target.outerText === 'Algorithms and Data structures') {
        navigate('/algorithmsAndDataStructures')
    } else if(event.target.outerText === 'Data visualization') {
        navigate('/dataVisualization')
    } else if(event.target.outerText === 'Physics') {
        navigate('/physics')
    } else if(event.target.outerText === 'Chess') {
        navigate('/chessBoard')
    }
  }

  return(
    <Flex
      flexWrap={'wrap'}
      justifyContent={'start'}
    >
      {items.map((item, i) => (
          <Box 
            w='fit-content' 
            h='40px' 
            bg='#10044d'
            cursor='pointer'
            _hover={{
                color: "tomato"
              }}
            key={i}
            borderRadius={'20px'} 
            textAlign={'center'}
            padding={'10px'}
            marginBottom={'10px'}
            marginRight={'5px'}
            onClick={handleClick}
          >
            {item.name}
          </Box>
      ))}
    </Flex>
  )
}

export default ProjectItems;
