import DungeonProblem from './DungeonProblem/DungeonProblem';
import { Tabs, TabList, TabPanels, Tab, TabPanel } from '@chakra-ui/react'
import Dijkstras from './Dijkstras/Dijkstras';

function Graphs() {

  return (
    <>
      <Tabs height={'100%'}>
        <TabList>
          <Tab>Dungeon Problem</Tab>
          <Tab>Dijkstra's</Tab>
        </TabList>
        <TabPanels height={'100%'}>
          <TabPanel>
            <DungeonProblem />        
          </TabPanel>
          <TabPanel>
            <Dijkstras />        
          </TabPanel>
        </TabPanels>
      </Tabs>
    </>
  )
  
}

export default Graphs