import { Box } from '@chakra-ui/react'
import GridCell from './GridCell';

function Grid2D(props) {
    
  return (
    <Box
      height={window.screen.width > 414 ? '400px': window.screen.width - 10}
      width={window.screen.width > 414 ? '400px': window.screen.width - 10}
      display='flex'
      flexWrap={'wrap'}
    >
      {props.elements.map((element, i) => {
        return  <GridCell 
                  key={i}
                  value={element.value}
                  type={element.type}
                  updateCell={props.updateCell}
                />
      })}
    </Box>
  )
}

export default Grid2D;