import { useRef, useEffect, useCallback } from 'react';
import Population from './animatedBarChartD3';
import classes from './populationD3.module.css';
import { Box, Heading } from '@chakra-ui/react';

function AnimatedBarChart() {
  let barChart = useRef();

  const draw =  useCallback(() => {
    new Population(barChart.current)
  }, [])

  useEffect(() => {
    draw();
        window.addEventListener('resize', draw);
        
        return () => {
            window.removeEventListener('resize', draw);
        }
    }, [draw])

  return (
    <Box  w='100%' marginLeft={'10px'}>
      <Heading as='h1' size='3xl' noOfLines={1} display="flex" justifyContent={'center'}>Data Visualization with D3</Heading>
      <Heading as='h2' size='3x1' noOfLines={1} display="flex" justifyContent={'start'}>Animated Barchart</Heading>
      <div ref={barChart} className={classes.chart}></div>
    </Box>
  )
}

export default AnimatedBarChart