import {Route, Routes} from 'react-router-dom';
import IntroPage from './containers/IntroPage';
import Header from './components/Header';
import './App.module.css';
import DSA from './containers/DSA';
import DataVisualization from './containers/DataVisualization';
import Physics from './containers/Physics';
import Chess from './containers/Chess';

function App() {
  return (
  <>
    <Header />
    <div>
       <br/><br/><br/><br/>
    </div>
    <Routes>
      <Route path="/" element={<IntroPage />} />
      <Route path="/algorithmsAndDataStructures" element={<DSA />} /> 
      <Route path="/dataVisualization" element={<DataVisualization />} />
      <Route path="/physics" element={<Physics />} />
      <Route path="/chessBoard" element={<Chess />} />
    </Routes>
  </>
  )
}

export default App;