import { useCallback, useEffect, useState } from "react";
import Grid2D from '../Grid2D';
import { Button, Text, Badge, Stack, Heading, ListItem, UnorderedList, Divider } from '@chakra-ui/react';

function DungeonProblem() {
    const [elements, setElements] = useState([])
  const [adjList, setAdjList] = useState()
  const [startNode, setStartNode] = useState()
  const [exitNode, setExitNode] = useState()
  const [instructions, setInstructions] = useState('Select a cell to start from the below grid')

  const createAdjacencyList = useCallback((cells) => {  
    let list = {};
    let neighbors = []
    let r,c;
    for (let i = 0; i < cells.length; i++) {
      r = cells[i].value.r;
      c = cells[i].value.c;
      list[`r${r}c${c}`] = []; 
    }
    for (let key in list) {
      neighbors = getNeighbors(key);
      for(let i = 0; i < neighbors.length; i++) {
        list[key].push(neighbors[i])
      }
    }
    setAdjList(list);
  }, [])

  const getNeighbors = (cell) => {
    let result = [];
    let dr = [-1, 1, 0, 0];
    let dc = [0, 0, 1, -1];
    let r, c;
    for (let i = 0; i < 4; i++) {
      r = parseInt(cell[1]) + dr[i];
      c = parseInt(cell[3]) + dc[i];

      if(r < 0 || c < 0 || r > 9 || c > 9) {
        // out of grid
      } else {
        result.push({r, c});
      }
    }
    return result;
  }

  const solveDungeonProblem = (startCell, exitCell) => {
    let rowQueue = [];
    let colQueue = [];
    let visited = {};
    let prev = {};
    let path = [];
    let reachedExit = false;
    for (let key in adjList) {
      visited[key] = false;
      prev[key] = null;
    }
    rowQueue.push(startCell.value.r);
    colQueue.push(startCell.value.c);
    visited[`r${startCell.value.r}c${startCell.value.c}`] = true;

    while (rowQueue.length > 0) {
      let r = rowQueue.shift();
      let c = colQueue.shift();
      if (r === exitCell.value.r && c === exitCell.value.c) {
        reachedExit = true;
        break;
      } else {
        exploreNeighbors(r, c, rowQueue, colQueue, visited, prev)
      }
    }
    path = getPathOnGrid(startCell, exitCell, prev);
    if(path.length !== 0 && reachedExit) {
      createPathOnGrid(path)
    } else {
      setInstructions("No way out");
    }
  }

  const exploreNeighbors = (r, c, rowQueue, colQueue, visited, prev) => {
    let dr = [-1, 1, 0, 0];
    let dc = [0, 0, 1, -1];
    let drDiag = [-1, -1, 1, 1];
    let dcDiag = [-1, 1, -1, 1];
    let newR, newC;
    // Top, down, left and right neigbors
    for (let i = 0; i < 4; i++) {
      newR = r + dr[i];
      newC = c + dc[i];
      if(newR < 0 || newC < 0 || newR > 9 || newC > 9 || isBlocked(newR, newC) || visited[`r${newR}c${newC}`]) {
        // out of grid or visited or blocked
      } else {
        rowQueue.push(newR);
        colQueue.push(newC);
        visited[`r${newR}c${newC}`] = true;
        prev[`r${newR}c${newC}`] = {type: 'open', value: {r: r, c: c}}
      }
    }
    // diagonal neighbors  
    for (let i = 0; i < 4; i++) {
      newR = r + drDiag[i];
      newC = c + dcDiag[i];
      if(newR < 0 || newC < 0 || newR > 9 || newC > 9 || isBlocked(newR, newC) || visited[`r${newR}c${newC}`]) {
        // out of grid or visited or blocked
      } else {
        rowQueue.push(newR);
        colQueue.push(newC);
        visited[`r${newR}c${newC}`] = true;
        prev[`r${newR}c${newC}`] = {type: 'open', value: {r: r, c: c}}
      }
    }
  }

  const isBlocked = (r, c) => {
    for (let i = 0; i < elements.length; i++) {
      if(elements[i].value.r === r && elements[i].value.c === c && elements[i].type === 'blocked') {
        return true;
      }
    }
    return false;
  }

  const getPathOnGrid = (startCell, exitCell, prev) => {
    let path = [];
    let cell = exitCell;
    while(prev[`r${cell.value.r}c${cell.value.c}`] !== null) {
      path.push(cell);
      cell = prev[`r${cell.value.r}c${cell.value.c}`];
    }
    path.reverse()
    if (path[path.length-1] === exitCell) {
      return path;
    } else {
      return [];
    }
  }

  const createPathOnGrid = (path) => {
    let gridElements = [...elements];
    for(let i = 0; i < gridElements.length; i++) {
      if(gridElements[i].type === 'path') {
        gridElements[i].type = 'open';
      }
    }
    for(let i = 0; i < gridElements.length; i++) {
      for(let j = 0; j < path.length; j++) {
        if(gridElements[i].value.r === path[j].value.r && gridElements[i].value.c === path[j].value.c) {
          if(gridElements[i].type !== 'exit') {
            gridElements[i].type = 'path'
          }
        }
      }
    }
    setElements(gridElements);
  }

  const generateGraphCells = useCallback(() => {
    let cells = [];
    for(let row = 0; row < 10; row++) {
      for(let col = 0; col < 10; col++) {
        cells.push({value: {r: row, c: col}, type: 'open'});
      }
    }
    createAdjacencyList(cells)
    return cells;
  }, [createAdjacencyList])

  useEffect(() => {
    setElements(generateGraphCells())
  }, [generateGraphCells])
  
  const updateCell = (value) => {
    if(instructions === 'Select a cell to start from the below grid') {
      setStartCell(value);
    } else if (instructions === 'Select exit cell') {
      setExitCell(value);
    } else if (instructions === 'Select cells to add rocks' || instructions === 'Click on Find Route button after adding rocks') {
      setBlockedCell(value);
    }
  }

  const setStartCell = (value) => {
    let newCells = [...elements];
    for (let i = 0; i < newCells.length; i++) {
      if (newCells[i].value.r === value.r && newCells[i].value.c === value.c) {
        newCells[i].type = 'start';
        setElements(newCells);
        setStartNode({value});
        setInstructions('Select exit cell');
        break;
      }
    }
  }

  const setExitCell = (value) => {
    let newCells = [...elements];
    for (let i = 0; i < newCells.length; i++) {
      if (newCells[i].value.r === value.r && newCells[i].value.c === value.c) {
        newCells[i].type = 'exit';
        setElements(newCells);
        setExitNode({value})
        setInstructions('Select cells to add rocks');
        break;
      }
    }
  }

  const setBlockedCell = (value) => {
    let newCells = [...elements];
    for (let i = 0; i < newCells.length; i++) {
      if (newCells[i].value.r === value.r && newCells[i].value.c === value.c) {
        if(newCells[i].type !== 'start' && newCells[i].type !== 'exit' && newCells[i].type !== 'blocked' && newCells[i].type !== 'path') {
          newCells[i].type = 'blocked';
          setElements(newCells);
          setInstructions('Click on Find Route button after adding rocks');
          break;
        } else if(newCells[i].type === 'blocked'){
            newCells[i].type = 'open';
            setElements(newCells);
            setInstructions('Click on Find Route button after adding rocks');
            break;
        } else if(newCells[i].type === 'path') {
            newCells[i].type = 'open';
            setElements(newCells);
            setInstructions('Click on Find Route button after adding rocks')
        }
      }
    }
  }

  const gridReset = () => {
    let initialGrid = [...elements];
    for(let i = 0; i < initialGrid.length; i++) {
      initialGrid[i].type = 'open';
    }
    setElements(initialGrid);
    setStartNode()
    setExitNode()
    setInstructions('Select a cell to start from the below grid');
  }
  
  return  (<>
            <Heading>The Dungeon Problem</Heading>
            <UnorderedList>
              <ListItem>You are trapped in a 2D dungeon and need to find the quickest way out! </ListItem>
              <ListItem>The dungeon has a size of R x C and you start at cell 'S' and there is an exit at cell 'E'.</ListItem>
              <ListItem> Cells full of rock are colored grey and empty cells are colored red.</ListItem>
              <ListItem>The starting cell is be marked as 'S'</ListItem>
              <ListItem>The ending cell is be marked as 'E'</ListItem>
              <ListItem>The path to is will be blue</ListItem>
              <ListItem>The path can be in any direction (up, down, right, left) and diagonally. </ListItem>
            </UnorderedList>
            <Divider orientation='horizontal' marginTop={'10px'} marginBottom={'10px'} />
            <Text> <strong>Algorithm used:</strong> Breadth First Search</Text>
            <UnorderedList>
            <ListItem>The Grid below is an unweighted graph, with each unit linked to its neighbors.</ListItem>
            </UnorderedList>
            <Text fontSize='2xl'>{instructions}</Text>
            <Stack direction='row' margin={'2px 2px 2px 2px'}>
              <Badge bg='#1c32bd' color="white">Path</Badge>
              <Badge bg='#cc471f' color="white">Empty</Badge>
              <Badge bg='#17bf60' color="white">Start</Badge>
              <Badge bg='#bee3f8'>Exit</Badge>
              <Badge bg='#615754' color="white">Rock</Badge>
            </Stack>
            <Grid2D elements={elements ? elements: []} updateCell={updateCell} />
            <Button
              margin={'2px 2px 2px 2px'} 
              colorScheme='blue'
              disabled={!startNode || !exitNode}
              onClick={() => solveDungeonProblem(startNode, exitNode)}
            >
              Find route
            </Button>
            <Button
              margin={'2px 2px 2px 2px'}
              colorScheme='blue'
              onClick={() => gridReset()}
            >
              Reset
            </Button>
          </>)
}

export default DungeonProblem;