import { Box } from '@chakra-ui/react'
import { useEffect, useState } from 'react';

function GridCell(props) {
  const [color, setColor] = useState('tomato')

  useEffect(() => {
    if(props.type === 'blocked') {
        setColor('#615754');
    } else if(props.type === 'path') {
        setColor('#1c32bd');
    } else if(props.type === 'open') {
        setColor('#cc471f');
    } else if(props.type === 'start') {
        setColor('#17bf60');
    } else if(props.type === 'exit') {
        setColor('#bee3f8')
    } 
  }, [color, props.type])

  return (
    <Box
        bg={color}
        height={window.screen.width > 414 ? '36px' : (2*window.screen.width - 10)/25}
        width={window.screen.width > 414 ? '36px' : (2*window.screen.width - 10)/25}
        margin={window.screen.width > 414 ? '2px' : window.screen.width/200}
        display='flex'
        alignItems={'center'}
        justifyContent={'center'}
        onClick={() => props.updateCell(props.value)}
        cursor="pointer"
    >
      {props.type === 'start' ? 'S': props.type === 'exit' ? 'E': ''}
    </Box>
  )
}

export default GridCell;