import React from 'react';

import {
    Box,
    Heading,
    Text,
    Flex,
    Accordion,
    AccordionItem,
    AccordionButton,
    AccordionPanel,
    AccordionIcon,
    UnorderedList,
    ListItem,
  } from '@chakra-ui/react';

import ProjectItems from '../components/ProjectItems/ProjectItems';
import SkillProfile from '../components/SkillProfile/SkillProfile';

function IntroPage() {
    const currentYear = new Date().getFullYear();
    const currentMonth = new Date().getMonth();
    const startYear = new Date(2019, 0, 0).getFullYear();
    const startMonth = new Date(2019, 1, 0).getMonth();
    let expYears, expMonths;
    if (currentMonth < startMonth) {
      expYears = currentYear - startYear - 2;
      expMonths = 11;
    } else {
      expYears = currentYear - startYear - 1;
      expMonths = currentMonth - startMonth - 1;
    }
    const experience = `${expYears} years ${expMonths > 0 ? `${expMonths} months`: ''}`;
    return (
      <>
        <Flex 
          flexWrap="wrap"
          justifyContent={'space-evenly'}
          width={'90%'}
          maxWidth={'1367.8px'}
        >
          <Flex 
            direction={'column'} 
            flexWrap={'wrap'}
            justifyContent={'flex-start'}
            minWidth={'330px'}
            width={'40%'}
            maxWidth={'546.9px'}
          >
            <Box 
              bg='#152c59' 
              w='100%' 
              p={4} 
              minWidth={'330px'}
              margin={'10px 0px 10px 0px'}
              color='white' 
              padding='10px'
              borderRadius='10px'
            >
              <Heading as="h2" size="lg" letterSpacing={"tighter"} paddingBottom={'20px'}>
                About me
              </Heading>
              <Text 
                fontSize='md'>
                I am a software developer, currently working at Cognizant Technology Solutions. I have about {experience} of experience in frontend web development with
                React JS and Redux along with HTML, CSS, SCSS, Javascript. My interests also include backend development, databases, algorithms, data structures,
                robotics and physics.

                I have some basic exposure to Node.js Express framework, MySQL database and Mongo DB.
              </Text>
            </Box>
            <Box
              bg='#152c59' 
              w='100%' 
              p={4} 
              color='white' 
              margin='10px 0px 10px 0px'
              padding='10px'
              minWidth='330px'
              borderRadius='10px'
            >
              <Heading as="h2" size="lg" letterSpacing={"tighter"} paddingBottom={'20px'}>
               Professional Experience and Projects
              </Heading>
              <Accordion defaultIndex={[0]} allowMultiple>
                <AccordionItem>
                  <h2>
                    <AccordionButton>
                      <Box as="div" flex='1' textAlign='left'>
                       <h3>Programmer Analyst</h3>
                       <h3>Cognizant, May 2021 - Present</h3> 
                      </Box>
                      <AccordionIcon />
                    </AccordionButton>
                  </h2>
                  <AccordionPanel>
                    <h4>Projects:</h4>
                    <p>
                      <p><strong>Verizon 5G Home Sales</strong></p>
                      E-commerce application where customers can place an order and pay for 5G or LTE internet
                      equipment and services.
                      I am working on the frontend which is built using React JS.
                      <UnorderedList>
                        <ListItem>
                            I have worked on development stories and defects involving redesign of existing pages and
                          adding new features and fixing issues in UI logic and accessibility.
                        </ListItem>
                      </UnorderedList>
                    </p>
                  </AccordionPanel>
                </AccordionItem>
                <AccordionItem>
                  <h2>
                    <AccordionButton>
                      <Box as="div" flex='1' textAlign='left'>
                        <h3>Software Engineer</h3>
                        <h3>AmberTAG Analytics, Feb 2019 - May 2021</h3>
                      </Box>
                      <AccordionIcon />
                    </AccordionButton>
                  </h2>
                  <AccordionPanel pb={4}>
                    <h4>Projects:</h4>
                    <Accordion defaultIndex={[0]} allowMultiple>
                      <AccordionItem>
                        <h5>
                          <AccordionButton>
                            <Box as="span" flex='1' textAlign='left'>
                              <strong>Online-Healthcare services project:</strong>
                            </Box>
                            <AccordionIcon />
                          </AccordionButton>
                        </h5>
                        <AccordionPanel pb={4}>
                        Patient management system where patients can book appointments with doctors online and 
                        consult over a video conference or book a regular appointment or a house visit from the doctor.
                        <UnorderedList>
                          <ListItem>
                            I worked on the UI of the admin part of the application
                          </ListItem>
                        </UnorderedList>
                        </AccordionPanel>
                      </AccordionItem>
                      <AccordionItem>
                        <h5>
                          <AccordionButton>
                            <Box as="span" flex='1' textAlign='left'>
                              <strong>Dashboard of a recommendation engine.</strong>
                            </Box>
                            <AccordionIcon />
                          </AccordionButton>
                        </h5>
                        <AccordionPanel>
                          A dashboard to show the performance of a recommendation engine.
                          <UnorderedList>
                            <ListItem>
                              Used D3.js library with React JS with Material UI component library in the front-end.
                            </ListItem>
                            <ListItem>
                              Worked on Django in the back-end with GraphQL and MySQL database.
                            </ListItem>
                          </UnorderedList>
                        </AccordionPanel>
                      </AccordionItem>
                    </Accordion>
                  </AccordionPanel>
                </AccordionItem>
                <AccordionItem>
                  <h4>
                    <AccordionButton>
                      <Box as="div" flex='1' textAlign='left'>
                        <h3>Software Intern</h3>
                        <h3>AmberTAG Analytics, Dec 2018 - Feb 2019</h3>
                      </Box>
                      <AccordionIcon />
                    </AccordionButton>
                  </h4>
                  <AccordionPanel>
                      <h4>Projects:</h4>
                      <p><strong>Call quality distribution and analysis in the USA</strong></p>
                      A map based web application which shows the call quality distribution in the United States, 
                      using MapBox API.
                      <UnorderedList>
                        <ListItem>
                          Used the MapBox API in the frontend to display the location of different calls and their quality 
                          on the map of the United States.
                        </ListItem>
                        <ListItem>
                          Used PHP to build a simple backend service along with MySQL database. 
                        </ListItem>
                      </UnorderedList>
                  </AccordionPanel>
                </AccordionItem>
              </Accordion>
            </Box>
            <Box 
              bg='#152c59' 
              w='100%' 
              p={4} 
              color='white' 
              margin='10px 0px 10px 0px'
              padding='10px'
              minWidth='330px'
              borderRadius='10px'
            >
              <Heading as="h2" size="lg" letterSpacing={"tighter"} paddingBottom={'20px'}>
              Personal Projects
              </Heading>
              <ProjectItems 
                items={[{name: 'Algorithms and Data structures', linkTo: "/algorithmsAndDataStructures"}, 
                        {name: 'Data visualization', linkTo: "/dataVisualization"}, 
                        {name: 'Physics', linkTo: "/physics"},
                        {name: 'Chess', linkTo: "/chessBoard"}]} 
              />
            </Box>
          </Flex>
          <Box 
            bg='#152c59' 
            w='40%'
            margin={'10px 0px 10px 0px'}
            color='white' 
            padding='10px'
            minWidth={'330px'}
            maxWidth={'546.9px'}
            borderRadius='10px'
          >
            <Heading as="h2" size="lg" letterSpacing={"tighter"} paddingBottom={'20px'}>
              My Skill Profile
            </Heading>
            <SkillProfile 
              items={[{name: 'React', score: 90}, {name: 'JavaScript', score: 80}, 
                      {name: 'HTML', score: 80}, {name: 'CSS, SCSS', score: 70}, 
                      {name: 'Python', score: 60}, {name: 'MySQL', score: 50},
                      {name: 'Node JS', score: 40}, {name: 'Java', score: 30}]} />
          </Box>
        </Flex>
      </>
    )
  }

export default IntroPage;
