import { Box, Image } from '@chakra-ui/react'

import whitePawn from '../../assets/chessPieces/white_pawn.png'
import blackPawn from '../../assets/chessPieces/black_pawn.png'
import whiteKnight from '../../assets/chessPieces/white_knight.png'
import blackKnight from '../../assets/chessPieces/black_knight.png'
import whiteBishop from '../../assets/chessPieces/white_bishop.png'
import blackBishop from '../../assets/chessPieces/black_bishop.png'
import whiteRook from '../../assets/chessPieces/white_rook.png'
import blackRook from '../../assets/chessPieces/black_rook.png'
import whiteQueen from '../../assets/chessPieces/white_queen.png'
import blackQueen from '../../assets/chessPieces/black_queen.png'
import whiteKing from '../../assets/chessPieces/white_king.png'
import blackKing from '../../assets/chessPieces/black_king.png'
import { useEffect, useState } from 'react'


function Piece(props) {
    const [pieceImage, setPieceImage] = useState(whitePawn);

    useEffect(() => {
        switch(props.piece) {
          case 'blackPawn':
            setPieceImage(blackPawn)
            break;
          case 'whitePawn':
            setPieceImage(whitePawn)
            break;
          case 'blackKnight':
            setPieceImage(blackKnight)
            break;
          case 'whiteKnight':
            setPieceImage(whiteKnight)
            break;
          case 'blackBishop':
            setPieceImage(blackBishop)
            break;
          case 'whiteBishop':
            setPieceImage(whiteBishop)
            break;
          case 'blackRook':
            setPieceImage(blackRook)
            break;            
          case 'whiteRook':
            setPieceImage(whiteRook)
            break;
          case 'blackQueen':
            setPieceImage(blackQueen)
            break;
          case 'whiteQueen':
            setPieceImage(whiteQueen)
            break;
          case 'blackKing':
            setPieceImage(blackKing)
            break;
          case 'whiteKing':
            setPieceImage(whiteKing)
            break;
          default :
            setPieceImage(whitePawn)
            break;
        }
    }, [props])


  return (
      <Box
        draggable='true'
        onDragEnd={props.dragEnd}
        onDragStart={props.dragStart}
      >
        <Image src={pieceImage}></Image>
      </Box>
  )
}

export default Piece