import * as d3 from 'd3';
import data from '../../assets/d3/newPopulation.json';
import "./animatedBarChart.css";
// set the dimensions and margins of the graph
var margin = {top: 20, right: 10, bottom: 40, left: 10},
width = 500 - margin.left - margin.right,
height = 350 - margin.top - margin.bottom;
const MARGIN = 1.5;
let COUNTRY_COUNT = 10;
const DELAY = 1000;
let INCLUDE_CONTINENTS = false;
let INCLUDE_WORLD = false;
let isMobile = false;

class populationD3 {
  constructor(element) {
    let rect = element.getBoundingClientRect();
    width = rect.width;
    isMobile = window.screen.width > 414 ? false: true;
    this.loopThroughYears(element, this.draw, this.getMaxForYear, this.topCountries, this.getCalenderYear);
  }

  topCountries (data, year, limit) {
    let countries = [];
    for (let i = 0; i < data.length; i++) {
      if(data[i]['Year'] === year) {
        for(let j = 0; j < data[i]['countries'].length; j++) {
          if(INCLUDE_CONTINENTS && INCLUDE_WORLD) {
            countries.push(data[i]['countries'][j]);  
          } else if(!INCLUDE_CONTINENTS && INCLUDE_WORLD 
                    && data[i]['countries'][j]['name'] !== "Asia" 
                    && data[i]['countries'][j]['name'] !== "Africa"
                    && data[i]['countries'][j]['name'] !== "Europe" 
                    && data[i]['countries'][j]['name'] !== "South America"
                    && data[i]['countries'][j]['name'] !== "North America") {
            countries.push(data[i]['countries'][j]);  
          } else if(INCLUDE_CONTINENTS && !INCLUDE_WORLD && data[i]['countries'][j]['name'] !== "World") {
            countries.push(data[i]['countries'][j]);
          } else if(!INCLUDE_CONTINENTS && !INCLUDE_WORLD 
            && data[i]['countries'][j]['name'] !== "Asia" 
            && data[i]['countries'][j]['name'] !== "Africa"
            && data[i]['countries'][j]['name'] !== "Europe" 
            && data[i]['countries'][j]['name'] !== "South America"
            && data[i]['countries'][j]['name'] !== "North America"
            && data[i]['countries'][j]['name'] !== "World") {
              countries.push(data[i]['countries'][j]);
          } 
        }
      }
    }
    countries = countries.filter((country) => country.population !== "NaN");
    countries = countries.sort(function(a, b) {
        return parseInt(a.population) - parseInt(b.population);
      });

    return countries.reverse().slice(0, limit);
  }

  getMaxForYear(year) {
    let popsInYear = [];
    let maxPop;
    for(let i = 0; i < data.length; i++) {
      if(data[i]['Year'] === year) {
        for( let j = 0; j < data[i].countries.length; j++) {
          if (data[i].countries[j].population !== "NaN") {
            if(INCLUDE_CONTINENTS && INCLUDE_WORLD) {
              popsInYear.push(parseInt(data[i].countries[j].population)/1000);
            } else if (!INCLUDE_CONTINENTS && INCLUDE_WORLD 
              && data[i]['countries'][j]['name'] !== "Asia" 
              && data[i]['countries'][j]['name'] !== "Africa"
              && data[i]['countries'][j]['name'] !== "Europe" 
              && data[i]['countries'][j]['name'] !== "South America"
              && data[i]['countries'][j]['name'] !== "North America") {
                popsInYear.push(parseInt(data[i].countries[j].population)/1000);
            } else if (INCLUDE_CONTINENTS && !INCLUDE_WORLD && data[i]['countries'][j]['name'] !== "World") {
                popsInYear.push(parseInt(data[i].countries[j].population)/1000);
            } else if (!INCLUDE_CONTINENTS && !INCLUDE_WORLD 
              && data[i]['countries'][j]['name'] !== "Asia" 
              && data[i]['countries'][j]['name'] !== "Africa"
              && data[i]['countries'][j]['name'] !== "Europe" 
              && data[i]['countries'][j]['name'] !== "South America"
              && data[i]['countries'][j]['name'] !== "North America"
              && data[i]['countries'][j]['name'] !== "World") {
                popsInYear.push(parseInt(data[i].countries[j].population)/1000);
            }
          }
        }
      }
    }
    maxPop = Math.max(...popsInYear);
    return maxPop;
  }

  loopThroughYears(element, draw, getMaxForYear, topCountries, getCalenderYear) {
    let years = [];
    let j = 0; 
    let d3Interval;
      
    d3.select('#startBtn').remove();
    d3.select('#pauseBtn').remove();
    d3.select('#restartBtn').remove();
    d3.select('#selectTopN').remove();
    d3.select('#btnGroup').remove();

    var buttonGroup = d3.select(element)
      .append("div")
      .attr('id', 'btnGroup')
      .attr("class", "buttonGroup") 

    var startButton = buttonGroup
      .append("input")
        .attr("id", "startBtn")
        .attr("type", "button")
        .attr("name", "Start")
        .attr("class", "startBtn")
        .attr("value", "Start")

    const initialChart = (j) => {
      if( j === 0) {
        d3.select(element).select("svg").remove();
        draw(element, years[j], getMaxForYear, topCountries, getCalenderYear);
      }
    }
    
    startButton.on("click", () => {
      d3Interval = d3.interval(animateChart, DELAY)
    })

    var pauseButton = buttonGroup
    .append("input")
        .attr("id", "pauseBtn")
        .attr("type", "button")
        .attr("name", "Pause")
        .attr("class", "pauseBtn")
        .attr("value", "Pause")

    pauseButton.on("click", () => {
      d3Interval.stop();
      })

    var restartButton = buttonGroup
    .append("input")
        .attr("id", "restartBtn")
        .attr("type", "button")
        .attr("name", "Restart")
        .attr("class", "restartBtn")
        .attr("value", "Restart")

    restartButton.on("click", () => {
      j = 0;
      d3Interval = d3.interval(animateChart, DELAY)
    })

    var countriesRange = buttonGroup
      .append("select")
        .attr("id", "selectTopN")
        .attr("name", "selectTopN")
        .attr("class", "selectTopN")
        .attr("value", "")
        countriesRange.append("option")
          .attr("value", "Select range")
          .attr("class", "selectFont")
          .attr("disabled", "true")
          .text("Select range")
        countriesRange.append("option")
          .attr("value", 5)
          .text("Top 5")
        countriesRange.append("option")
          .attr("value", 10)
          .attr("selected", "selected")
          .text("Top 10")
        countriesRange.append("option")
          .attr("value", 20)
          .text("Top 20")

        countriesRange.on("change", (e) => {
          if (e.target.selectedIndex === 1) {
            COUNTRY_COUNT = 5;
            d3Interval.stop();
            d3Interval = d3.interval(animateChart, DELAY);
          } else if(e.target.selectedIndex === 2) {
            COUNTRY_COUNT = 10;
            d3Interval.stop();
            d3Interval = d3.interval(animateChart, DELAY);
          } else if(e.target.selectedIndex === 3) {
            COUNTRY_COUNT = 20;
            d3Interval.stop();
            d3Interval = d3.interval(animateChart, DELAY);
          } 
        })

    const animateChart = () => {
      if(j < data.length) {
        d3.select(element).select("svg").remove();
        draw(element, years[j], getMaxForYear, topCountries, getCalenderYear);
        j++;
      }
    }
    for(let i = 0; i < data.length; i++) {
      if(data[i]['Year'] !== 'NaN') {
        years.push(data[i]['Year']);
      }
    }
    initialChart(j);
  }

  getCalenderYear (year) {
    if (year <= 0) {
      return -year + " BCE";
    } else {
      return year + " CE";
    }
  }

  draw (element, year, getMaxForYear, topCountries, getCalenderYear) {

    var svg = d3.select(element)
      .append("svg")
        .attr("width", width - margin.right - margin.left)
        .attr("height", height + margin.top + margin.bottom)
      .append("g")
        .attr("transform","translate(70, 20)");

        // Add X axis
        var x = d3.scaleLinear()
        .domain([0, getMaxForYear(year)*MARGIN])
        .range([ 0, width])
        svg.append("g")
        .attr("transform", "translate(0," + height + ")")
        .call(d3.axisBottom(x).ticks(5)
        .tickFormat(d3.format(".2s")))
        .selectAll("text")
          .style("text-anchor", "end")
    
        let newData = topCountries(data, year, COUNTRY_COUNT);
        // Y axis
        var y = d3.scaleBand()
          .range([ 10, height ])
          .domain(newData.map(function(d) { return d.name; }))
          .padding(.1);
          svg.append("g")
          .call(d3.axisLeft(y))

          var colors = d3.scaleOrdinal()
          .domain(['India','China','United Kingdom', 'France', 'United States', 'Indonesia','Brazil','Spain','Russia', 'Nigeria','Afghanistan','Pakistan','Iran', 'Eritrea & Ethiopia', 'Bangladesh', 'Japan', 'Germany', 'Italy', 'Turkey', 'Poland', 'Mexico'])
          .range(['#f56f42', '#f51616', '#0f46d1', '#318CE7', '#002868', 'lime', 'green', '#b5c22d', '#0f3f75', '#31750f', '#d10f1c', '#0b2e07', '#558050', '#ede209', '#337820', '#75726b', '#e0310d', '#f58e8e', '#9fd94e', '#bf1b15', '#BB8A0C'])

        //Bars
        svg.selectAll("myRect")
        .data(newData)
        .enter()
        .append("rect")
        .attr("x", x(0))
        .attr("y", function(d) { return y(d.name); })
        .attr("width", function(d) { return x((parseInt(d.population)/1000).toFixed(1)); })
        .attr("height", y.bandwidth())
        .attr("fill", (d, i) => colors(d.name))
        // .append("text")
        //   .attr("x", function(d) { return x(100); })
        //   .attr("y", function(d) { return y(d.name); })
        //   .attr("text-anchor", "end")
        //   .style("font-size", "10px")
        //   .attr("class", "checkthis")
        //   .text((d) => d.population)
        // value at the end of each bar
        if(!isMobile) {
          var texts = svg.selectAll(".myTexts")
          .data(newData)
          .enter()
          .append("text");

          texts.attr("x", function(d){ return x((parseInt(d.population)/1000).toFixed(1))+50})
              .attr("y", function(d,i){ return y(d.name) + y.bandwidth()/2 + 4; })
              .attr("text-anchor", "end")
            .style("font-size", "10px")
              .text((d) => (parseInt(d.population)/1000).toFixed(1));
        }

        svg.append("text")
        .attr("x", window.screen.width > 414 ? 105 : margin.left)             
        .attr("y", 0)
        .attr("text-anchor", "middle")  
        .style("font-size", "16px") 
        .style("text-decoration", "underline")  
        .text(window.screen.width > 414 ? `Top ${COUNTRY_COUNT} countries by population in year ` + getCalenderYear(year) : `Top ${COUNTRY_COUNT} in ${getCalenderYear(year)}`);

        svg.append("text")
          .attr("class", "x label")
          .attr("text-anchor", "end")
          .attr("x", width/2)
          .attr("y", height + 35)
          .text("Population 1000x");
          
        // .attr("x", function(d) { return x(d.Country); })
        // .attr("y", function(d) { return y(d.Value); })
        // .attr("width", x.bandwidth())
        // .attr("height", function(d) { return height - y(d.Value); })
        // .attr("fill", "#69b3a2")
  }
}

export default populationD3;